// app/components/LogoutButton.tsx
'use client';

import { signOut } from 'next-auth/react';

export default function LogoutButton() {
  return (
    <button
      onClick={() => signOut({ callbackUrl: '/' })}
      className="text-white bg-red-600 px-4 py-2 rounded"
    >
      Logout
    </button>
  );
}
